*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --primary-text-color: #000;
  --secondary-text-color: #8c8c8c;
  --primary-white-color: #fff;
  --line-color: #ff001b;
  --button-activ-color: #ff6b08;
  --footer-background: #f7f7f7;
  --footer-text-color: #545454;
  --background-color: #fff5e9;
  --time-function: cubic-bezier(.4, 0, .2, 1);
  --card-gap: 16px;
  --scroll-width: 25px;
}

body {
  background-color: var(--background-color);
  font-family: Roboto, sans-serif;
  font-style: normal;
}

body.modal-open {
  overflow: hidden;
}

html {
  box-sizing: border-box;
}

*, :after, :before {
  box-sizing: inherit;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

address {
  font-style: normal;
}

button {
  cursor: pointer;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link {
  color: inherit;
  text-decoration: none;
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 90000;
  transition: opacity 250ms var(--time-function), visibility 250ms var(--time-function);
  background-color: rgba(0, 0, 0, .25);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is_hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.button {
  width: 152px;
  height: 44px;
  border: 1px solid var(--primary-white-color);
  color: var(--primary-white-color);
  text-transform: uppercase;
  transition: background-color 250ms var(--time-function), border 250ms var(--time-function);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 5px;
  padding: 14px 0 12px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33333;
  display: inline-block;
}

.button:hover, .button:focus, .button.active {
  background-color: var(--button-activ-color);
  border: 1px solid var(--button-activ-color);
  filter: drop-shadow(0 8px 43px rgba(255, 107, 1, .6));
}

.button-modal {
  width: 110px;
  height: 44px;
  border: 1px solid var(--primary-text-color);
  color: var(--primary-text-color);
  background-color: var(--primary-white-color);
  text-transform: uppercase;
  transition: color 250ms var(--time-function), background-color 250ms var(--time-function), border 250ms var(--time-function);
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 13px 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33333;
  display: inline-flex;
}

.button-modal:hover, .button-modal:focus {
  background-color: var(--button-activ-color);
  border: 1px solid var(--button-activ-color);
  color: var(--primary-white-color);
  filter: drop-shadow(0 8px 43px rgba(255, 107, 1, .6));
}

@media screen and (min-width: 768px) {
  .button {
    width: 130px;
  }

  .button-modal {
    width: 124px;
    padding: 14px 0 12px;
  }

  .button-modal.modal-activ {
    padding-left: 0;
    padding-right: 0;
    display: inline-block;
  }
}

