.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.loader, .loader:before, .loader:after {
  width: 2.5em;
  height: 2.5em;
  border-radius: 50%;
  animation: load7 1.8s ease-in-out infinite;
}

.loader {
  color: var(--button-activ-color);
  text-indent: -9999em;
  margin: 10px auto;
  font-size: 8px;
  animation-delay: -.16s;
  position: relative;
  transform: translateZ(0);
}

.loader:before, .loader:after {
  content: "";
  position: absolute;
  top: 0;
}

.loader:before {
  animation-delay: -.32s;
  left: -3.5em;
}

.loader:after {
  left: 3.5em;
}

@keyframes load7 {
  0%, 80%, 100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }

  40% {
    box-shadow: 0 2.5em;
  }
}

@keyframes shine {
  from {
    opacity: 0;
    left: 0%;
  }

  50% {
    opacity: 1;
  }

  to {
    opacity: 0;
    left: 100%;
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0deg)translate3d(0, 0, 0);
  }

  25% {
    transform: rotate(3deg)translate3d(0, 0, 0);
  }

  50% {
    transform: rotate(-3deg)translate3d(0, 0, 0);
  }

  75% {
    transform: rotate(1deg)translate3d(0, 0, 0);
  }

  100% {
    transform: rotate(0deg)translate3d(0, 0, 0);
  }
}

@keyframes jelly {
  25% {
    transform: scale(.9, 1.1);
  }

  50% {
    transform: scale(1.1, .9);
  }

  75% {
    transform: scale(.95, 1.05);
  }
}

@keyframes pulse {
  from {
    box-shadow: 0 0 0 0 var(--button-activ-color);
  }
}

